import React from "react";
import Banner from "./Banner";
import { Splide, SplideSlide } from "@splidejs/react-splide";
import { sliderOptions } from "../../../../components/options";

function BlockSlider({ slider, smallSize, mediumSize }) {
	if (!slider) return "";

	return slider ? (
		<div className="block-slider">
			<Splide
				className="splide"
				options={{ sliderOptions, pagination: false }}
			>
				{slider.photos.map((item, index) => {
					return (
						<SplideSlide key={index}>
							<Banner
								item={item}
								size={"original"}
								smallsize={smallSize}
								mediumSize={mediumSize}
							/>
						</SplideSlide>
					);
				})}
			</Splide>
		</div>
	) : null;
}

export default React.memo(BlockSlider);
