import React, {
	// useEffect,
	useMemo,
	useState,
} from "react";
import { Helmet } from "react-helmet";
import Slider from "../components/banners/Slider";
import { useSelector } from "react-redux";
import { Link } from "react-router-dom";
import { useTranslation } from "react-i18next";
import MiniSlider from "../components/banners/MiniSlider";
import BrandThree from "../components/brands/BrandThree";
import CollectionsThree from "../components/collections/CollectionsThree";
import Banners from "../components/banners/Banners";
import BlockBannerCollection from "../components/blocks/BlockBannerCollection";
import QuickView from "../../../components/features/product/QuickViewNew";
import BlockSlider from "../components/banners/BlockSlider";
import SuppliersListTwo from "../components/suppliers/SuppliersListTwo";
import { getImageUrl } from "../../../utils/oxMethods";
// import PreLoader from "../../../components/pre-loaders";

function HomePageSix() {
	const marketData = useSelector((state) => state.marketData);
	const { blocks } = useSelector((state) => state.blocks);
	const [showText, setShowText] = useState(false);
	const { t } = useTranslation();
	const description = marketData?.settings?.seoHtml;
	const head = useMemo(() => {
		return {
			title: marketData.name,
			description: marketData.settings && marketData.settings.description,
			keyword: marketData.settings && marketData.settings.keyword,
			favicon: getImageUrl(marketData.settings.faviconImage, "50x_"),
		};
	}, []);

	if (!blocks) {
		return "";
	}

	// const menus = useSelector(state => state.menu);
	// console.log(menus);

	const miniBanners = blocks.find(
		(block) => block.title === "advertising_banners"
	);
	const miniBlock = blocks.find((block) => block.title === "mini_block");
	const sliderBlock = blocks.find((block) => block.title === "slider_block");
	const bigBlocks = blocks.find((block) => block.title === "big_block");
	const bigBlocksTwo = blocks.find(
		(block) => block.title === "big_block_two"
	);
	const bigBlocksThree = blocks.find(
		(block) => block.title === "big_block_three"
	);
	const brands = blocks.find((block) => block.title === "brands");
	const latestCollection = blocks.find((block) => block.title === "Latest");
	const blockRecommended = blocks.find(
		(block) => block.title === "recomended"
	);

	return (
		<>
			<Helmet>
				<title>{head.title}</title>
				<meta name="description" content={head.description} />
				<meta name="keywords" content={head.keyword} />
				<meta name="apple-mobile-web-app-title" content={head.title} />
				<meta name="application-name" content={head.title} />
				<link
					rel="icon"
					type="image/png"
					href={head.favicon}
					sizes="16x16"
				/>
			</Helmet>

			<h1 className="d-none">{head.title}</h1>

			<div className="home-page-six">
				<div className="container slider-container">
					<Slider />
				</div>
				<div className="container mini-sliders-container">
					<MiniSlider block={miniBlock} />
				</div>
				<div className="container">
					<Banners block={miniBanners?.dynamicCollectionBlock} />
				</div>
				<div className="container block-collection-container">
					<BlockBannerCollection
						block={bigBlocks}
						menuCol={"col-lg-2 col-12"}
						bannerCol={"col-md-3 col-0"}
						collectionCol={"col-lg-7 col-12"}
					/>
				</div>
				<div className="container slider-container">
					<BlockSlider
						slider={sliderBlock?.dynamicCollectionBlock[0]}
						smallSize={"800x_"}
					/>
				</div>

				<div className="container block-collection-container">
					<BrandThree block={brands} />
					<CollectionsThree
						collection={latestCollection}
						title={"new-items"}
					/>

					<BlockBannerCollection
						block={bigBlocksTwo}
						menuCol={"col-lg-2 col-12"}
						bannerCol={"col-md-3 col-0"}
						collectionCol={"col-lg-7 col-12"}
					/>

					<CollectionsThree
						collection={blockRecommended}
						title={"recommended"}
					/>

					<BlockBannerCollection
						block={bigBlocksThree}
						menuCol={"col-lg-2 col-12"}
						bannerCol={"col-md-3 col-0"}
						collectionCol={"col-lg-7 col-12"}
					/>
				</div>

				<div className="container suppliers-container">
					<SuppliersListTwo />
				</div>
				<div className="container">
					<div className="mb-4">
						<div
							className={`before-footer-text ${
								showText ? "active" : ""
							}`}
							dangerouslySetInnerHTML={{ __html: description }}
						/>
						<Link
							to="#"
							className={`opener ${
								showText ? "d-none" : "d-inline-block"
							}`}
							onClick={() => setShowText(!showText)}
						>
							{t("read-more")}
						</Link>
					</div>
				</div>

				<QuickView />
			</div>
		</>
	);
}

export default HomePageSix;
