import { Splide, SplideSlide } from "@splidejs/react-splide";
import React, { useEffect } from "react";
import { useTranslation } from "react-i18next";
import { useDispatch, useSelector } from "react-redux";
import { setSuppliers } from "../../../../actions/suppliers";
import SupplierItemTwo from "./SupplierItemTwo";
import { getSuppliersList } from "../../../../api/cache/main";
import product from "./mockProduct.json";
import QueryString from "qs";

function SuppliersListTwo() {
	const { suppliers } = useSelector(state => state.suppliers);
	const dispatch = useDispatch();
	const { t } = useTranslation();

	useEffect(() => {
		if (suppliers) {
			getSuppliersList(QueryString.stringify({ page: 1, size: 10 })).then(
				res => {
					const suppliers = [];
					for (const i of res.items) {
						if (!i.products) {
							i.products = [product];
							suppliers.push(i);
						} else suppliers.push(i);
					}
					dispatch(setSuppliers(suppliers));
				}
			);
		}
	}, []);

	return (
		<div className="all-suppliers">
			{suppliers?.length > 0 ? (
				<>
					<div className="market-header-all">
						<h3>{t("all-suppliers")}</h3>
					</div>
					<div className="wrapper-all-suppliers position-relative">
						<div className="position-absolute gradient-div" />
						<div className="all-market-scroller">
							<Splide
								className="splide"
								options={{
									rewind: true,
									pagination: false,
									arrows: false,
									perPage: 2.2,
									focus: "left",
									gap: "10px",
									breakpoints: {
										560: {
											perPage: 1.2
										}
									}
								}}
							>
								{suppliers?.map((item, i) => {
									return (
										<SplideSlide key={i}>
											<SupplierItemTwo supplier={item} />
										</SplideSlide>
									);
								})}
							</Splide>
						</div>
					</div>
				</>
			) : null}
		</div>
	);
}

export default SuppliersListTwo;
