import React from "react";
import ShimmerProduct from "../../../../components/features/loaders/shimmer";
import { connect, useSelector } from "react-redux";
import Banner from "../banners/Banner";
import { Splide, SplideSlide } from "@splidejs/react-splide";
import { brandSlider } from "../../../../components/options";

function BrandThree() {
	const { blocks } = useSelector(state => state.blocks);

	const block = blocks?.find(block => block.title === "brands");

	if (!block) {
		[1, 2, 3, 4, 5].map((i, index) => <ShimmerProduct key={index} />);
	}
	return block?.dynamicCollectionBlock?.length > 0 ? (
		<Splide className="splide brands" options={brandSlider}>
			{block.dynamicCollectionBlock[0].photos?.map((item, index) => {
				return (
					<SplideSlide key={index}>
						<Banner item={item} />
					</SplideSlide>
				);
			})}
		</Splide>
	) : null;
}

function mapStateToProps(state) {
	return state;
}

export default connect(mapStateToProps)(BrandThree);
